import { NotificationService } from './../../../../../shared/services/notification.service';
import { OrderPipe } from 'ngx-order-pipe';
import { GlobalService } from './../../../../../shared/services/global.service';
import { HeaderService } from './../../../../../shared/services/header.service';
import { Subscription } from 'rxjs';
import { Component, OnInit, ViewChild, HostListener, OnDestroy } from '@angular/core';
import { FormControl, FormGroup, NgForm } from '@angular/forms';
import { BsDatepickerDirective } from 'ngx-bootstrap';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-profit-loss',
  templateUrl: './profit-loss.component.html',
  styleUrls: ['./profit-loss.component.scss']
})
export class ProfitLossComponent implements OnInit, OnDestroy {
  public isVisibleFormArea = false;
  public actionOnPosition: number[] = [0, 1, 2];
  public LoggedParams: any;
  public isLoading = false;
  public liveAccountData: any[] = [];
  public stopLossOrderData: any[] = [];
  public subscription1: Subscription;
  public subscription2: Subscription;
  public subscription3: Subscription;
  public direction: number;
  public isDirectionSelected = false;
  public defaultPushMail = false;
  public defaultDisableTrade = false;
  public defaultLimitType = 0;
  public IsDataNotFound = false;
// Display Message Property
  messageToDisplay: string;
  ErrorMsqBlock = false;
  successMsqBlock = false;

   // pagination Setting
  public TotalCount = 0;
  public cat;
  public searchText: any; // Search Text property is used to filter text in table
  public p: any;          // Current page in pagination
  public pageSize = 5;  // Table Rows Perpage property
  public sortedData: any[]; // Sorted Product list array
  public order = '';     // Order property to sort coloum of table
  public reverse = true; // Reverse property is used to sort table coloum
  public setParams: any;
  @ViewChild('formData', {static: false}) stopLossOrderForm: NgForm;
  isOpenDatePicker = false;
  currentDate = new Date();
  minDate: Date;
  maxDate: Date;
  @ViewChild(BsDatepickerDirective, { static: false }) datepicker: BsDatepickerDirective;

  form = new FormGroup({
    dateYMD: new FormControl(new Date()),
    dateFull: new FormControl(new Date()),
    dateMDY: new FormControl(new Date()),
    dateRange: new FormControl([
      new Date(),
      new Date(this.currentDate.setDate(this.currentDate.getDate() + 7))
    ])
  });

  constructor(
    private headerService: HeaderService,
    private globalService: GlobalService,
    private orderPipe: OrderPipe,
    private notificationService: NotificationService
  ) {
    this.minDate = new Date();
    this.maxDate = new Date();
    this.minDate.setDate(this.minDate.getDate() - 0);
    this.maxDate.setDate(this.maxDate.getDate() + 2);

    this.subscription1 = this.headerService.castLoggedUserDetail.subscribe(
      resData => (this.LoggedParams = resData)
    );
  }
  toggleFormArea() {
    this.isVisibleFormArea = !this.isVisibleFormArea;
  }
  @HostListener('window:scroll')
  onScrollEvent() {
    this.datepicker.hide();
  }
  setOrder(value: string) {
    if (this.order === value) {
      this.reverse = !this.reverse;
    }
    this.order = value;
  }
  ngOnInit(): void {
    this.getProfileAccounts();
    this.getStopLossOrderData(1, this.cat);
  }
  getStopLossOrderData(indexval, countVal) {
      this.isLoading = true;
      if (countVal) {
        this.pageSize = indexval;
        this.p = 1;
        this.setParams = {
          Profile: this.LoggedParams.sProfile.sMasterId,
          Index: this.p,
          Count: this.pageSize
        };
      } else {
        this.p = indexval;
        this.setParams = {
          Profile: this.LoggedParams.sProfile.sMasterId,
          Index:  (this.p - 1) * this.pageSize + 1,
          Count: this.p * this.pageSize
        };
      }
      console.log(this.LoggedParams.sProfile.sMasterId);
      this.subscription3 = this.globalService.getRiskPL(this.setParams).subscribe(
        data => {
          if (data.Count > 0) {
            this.TotalCount = data.Count;
            this.stopLossOrderData = data.lstEquityDD;
            this.isLoading = false;
            this.IsDataNotFound = false;
          } else {
            this.IsDataNotFound = true;
            this.isLoading = false;
          }
        }
      );
    }

  getProfileAccounts() {
    const params = {
      ProfileId: this.LoggedParams.sProfile.sMasterId,
      LoginId: this.LoggedParams.sProfile.sMasterId
    };
    this.subscription2 = this.globalService.GET_PROFILE_ACCOUNTS(params).subscribe(
      data => {
        this.liveAccountData = data.lstAccount.map( acc => {
          if (acc) {
            return Object.assign(acc.sUserInfo, acc.sAccountPlan);
          }
        });
      }
    );
  }
  resetForm() {
    this.stopLossOrderForm.reset();
    this.isDirectionSelected = false;
    // this.stopLossOrderForm.value.limitType = 0;
    // this.stopLossOrderForm.form.value.limitType = 0;
    // this.defaultLimitType = 0;
  }
  onSubmit() {
    // alert(this.stopLossOrderForm.value.expiryTime);
    this.isLoading = true;
    const params = {
      RiskId: 0,
      Profile: this.LoggedParams.sProfile.sMasterId.toString(),
      Account: this.stopLossOrderForm.value.accountnumber.toString(),
      Value: this.stopLossOrderForm.value.targetLimit.toString(),
      sValue: 0,
      oPerc_Value: this.stopLossOrderForm.value.limitType,
      oUP_DOWN: this.direction,
      oAction: this.stopLossOrderForm.value.actionOnPosition,
      oAlert_Mail: (this.stopLossOrderForm.value.pushEmail === false) ? 0 : 1,
      oAlert_Disable_Trade: (this.stopLossOrderForm.value.disableTrade === false ) ? 0 : 1,
      oTimestamp: {
        sCreadedOn: 0,
        sUpdatedOn: 0,
        sCreadedOn_Str: new Date(),
        sUpdatedOn_Str: new Date(),
      },
      oExpiry: {
        sCreadedOn: 0,
        sUpdatedOn: 0,
        sCreadedOn_Str: this.stopLossOrderForm.value.expiryTime,
        sUpdatedOn_Str: 0,
      }
    };
    this.globalService.addRiskPL(params).subscribe(
      data => {
        if (data.Result !== -1) {
          // console.log(data);
          this.getStopLossOrderData(1, this.cat);
          this.resetForm();
          // this.onSuccessMsqDisplay('Profit loss DD alert added successfully');
          this.notificationService.showSuccess('Profit loss drawdown alert added successfully', 'Success');
          // this.autoCloseMsqBlock();
          this.isLoading = false;
          // this.isVisibleFormArea = false;
        } else {
          // this.onErrorMsqDisplay('Failed to add Profit loss DD');
          this.notificationService.showError('Failed to add Profit loss drawdown alert', 'Error');
          this.isLoading = false;
          // this.autoCloseMsqBlock();
        }
      }
    );

  }
  onSelectDirection(direction: number) {
    this.direction = direction;
    this.isDirectionSelected = true;
  }

  onDelete(selectedItem) {
    Swal.fire({
      title: 'Are you Sure!',
      text: 'Are you sure to delete this drawdown alert?',
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel',
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
    }).then((result) => {
      if (result.value) {
        const params = {
          RiskId: selectedItem.RiskId,
        };
        console.log(params);
        this.globalService.deleteRiskPL(params).subscribe(data => {
          if (data.Result === true) {
            this.getStopLossOrderData(1, this.cat);
            Swal.fire(
              'Deleted!',
              'Your drawdown alert has been deleted.',
              'success'
            );
          }
        });
      }
    });
  }
  autoCloseMsqBlock() {
    setTimeout(() => {
      this.successMsqBlock = false;
      this.ErrorMsqBlock = false;
      this.messageToDisplay = '';
    }, 10000);
  }
  onCloseMsqBlock() {
    this.successMsqBlock = false;
    this.ErrorMsqBlock = false;
    this.messageToDisplay = '';
  }
  onSuccessMsqDisplay(msq: string) {
    this.messageToDisplay = '';
    this.ErrorMsqBlock = false;
    this.successMsqBlock = true;
    this.messageToDisplay = msq;
  }
  onErrorMsqDisplay(msq: string) {
    this.messageToDisplay = '';
    this.successMsqBlock = false;
    this.ErrorMsqBlock = true;
    this.messageToDisplay = msq;
  }
  ngOnDestroy() {
    this.subscription1.unsubscribe();
    this.subscription2.unsubscribe();
    this.subscription3.unsubscribe();
  }

}
