import { ProfitLossComponent } from './risk-manager-core/profit-loss/profit-loss.component';
import { SymbolDrawdownComponent } from './risk-manager-core/symbol-drawdown/symbol-drawdown.component';
import { EquityDrawdownComponent } from './risk-manager-core/equity-drawdown/equity-drawdown.component';
import { RiskManagerCoreComponent } from './risk-manager-core/risk-manager-core.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  {
    path: '',
    component: RiskManagerCoreComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        redirectTo: 'equity-drawdown'
      },
      {
        path: 'equity-drawdown',
        component: EquityDrawdownComponent,
        data: {title: 'Risk Manager', breadcrumb: 'Equity Drawdown'}
      },
      {
        path: 'symbol-drawdown',
        component: SymbolDrawdownComponent,
        data: {title: 'Risk Manager', breadcrumb: 'Symbol Drawdown'}
      },
      {
        path: 'profit-loss',
        component: ProfitLossComponent,
        data: {title: 'Risk Manager', breadcrumb: 'Profit Loss'}
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RiskManagerRoutingModule { }
